import React, {Component } from 'react'
import {Link,Route} from 'react-router-dom'
import All from './All'
import Cream from './Cream'
import Share from './Share'
import Come from './Come'
export default class Home extends Component {
    constructor(){
        super();
        this.state = {
            data: []
        }
    }
    componentDidMount(){
        let page = this.props.match.params.page;
        fetch('https://cnodejs.org/api/v1/topics?page='+page)
        .then((res)=>res.json())
        .then((res)=>{
            this.setState({data:res.data});
        })
    }
 
    componentDidUpdate(prevProps,prevState){
        if(prevProps.match.params.page!==this.props.match.params.page){
            let page = this.props.match.params.page;
            fetch('https://cnodejs.org/api/v1/topics?page='+page)
                .then((res)=>res.json())
                .then((res)=>{
                        this.setState({data:res.data});
                    })
        }
    }
        render() {
            let url = this.props.match.url
            return (
                <div>
                    <div className='title'>
                        <Link to={url+'/all'} style={{color:"#80bd01",marginLeft:"20px",fontSize:"16px"}}>全部</Link>
                        <Link to={url+'/cream'} style={{color:"#80bd01",marginLeft:"20px",fontSize:"16px"}}>精华</Link>
                        <Link to={url+'/share'} style={{color:"#80bd01",marginLeft:"20px",fontSize:"16px"}}>分享</Link>
                        <Link to={url+'/question'} style={{color:"#80bd01",marginLeft:"20px",fontSize:"16px"}}>问答</Link>
                        <Link to={url+'/employ'} style={{color:"#80bd01",marginLeft:"20px",fontSize:"16px"}}>招聘</Link>
                        <Link to={url+'/test'} style={{color:"#80bd01",marginLeft:"20px",fontSize:"16px"}}>客户端测试</Link>
                    </div>
                    <div>
                        <Route path={`${url}/all`} component={All} />
                        <Route path={`${url}/cream`} component={Cream} />
                        {/* <Route path='/home/:tab' component={List}/> */}
                        <Route path={`${url}/share`} component={Share}/>
                    </div>
                    <button className='pages'>1</button>
                    <button className='pages'>2</button>
                    <button className='pages'>3</button>
                    <button className='pages'>4</button>
                    <button className='pages'>5</button>
                    <button className='pages'>6</button>
                    <button className='pages'>7</button>
                    <button className='pages'>8</button>
                    <button className='pages'>9</button>
                    <button className='pages'>10</button>
                    
                </div>
            )
        }
}
