import React, { Component } from 'react'
import {Link,Route} from 'react-router-dom'
import Home from './Home'
export default class Come extends Component {
    render() {
        return (
            <div>
                <div className='signin'>
                <span>用户名&nbsp;&nbsp;</span>
                <input type='text' style={{border:"1px solid #444",borderRadius:'3px',width:"30%",height:'30px', marginBottom:'20px',marginTop:"30px",textAlign:"center"}}></input><br></br>
                <span style={{marginBottom:"20px"}}>密码&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <input type='password' style={{border:"1px solid #444",borderRadius:'3px',width:"30%",height:'30px',marginBottom:'20px'}}></input>
                </div>
                <div>
                    <Link to='/home' style={{border:"1px solid #05c",backgroundColor:"#05c",color:'#fff',borderRadius:'3px'}}>登录</Link>
                    <Route path='/home' component={Home}/>
                </div>   
            </div>
        )
    }
}
