import React, { Component } from 'react'
import {Link,Route} from 'react-router-dom'
import All from './All'
import Cream from './Cream';
export default class Home extends Component {
    constructor(){
        super();
        this.state = {
            data: []
        }
    }
    componentDidMount(){
        let page = this.props.match.params.page;
        fetch('https://cnodejs.org/api/v1/topics?page='+page)
        .then((res)=>res.json())
        .then((res)=>{
            this.setState({data:res.data});
        })
    }
 
    componentDidUpdate(prevProps,prevState){
        if(prevProps.match.params.page!==this.props.match.params.page){
            let page = this.props.match.params.page;
            fetch('https://cnodejs.org/api/v1/topics?page='+page)
                .then((res)=>res.json())
                .then((res)=>{
                        this.setState({data:res.data});
                    })
        }
    }
        render() {
            let url = this.props.match.url
            return (
                <div>
                    <div>
                        <Route path={`${url}/all`} component={All} />
                        <Route path={`${url}/cream`} component={Cream} />
                        {/* <Route path='/home/:tab' component={List}/> */}
                    </div>
                    {
                        this.state.data.map((item)=>(
                            <ul>
                                <li className='boxli'>
                                    <img src={item.author.avatar_url} style={{width:'30px',height:'30px',float:"left"}}/>
                                    <p className="boxp">{item.reply_count}/{item.visit_count}</p>
                                    <Link to={'/topics/'+item.id} style={{marginLeft:"20px",color:"#778087",fontSize:"14px"}}>{item.title}</Link>
                                    <span style={{float:'right'}}><img src={item.author.avatar_url} style={{width:'30px',height:'30px',}} />
                                    1分钟前
                                    </span> 
                                </li>
                            </ul>
                            
                           
                        ))
                    }
                </div>
            )
        }
}